.t {
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #05445E;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.t-icon {
    width: 15px;
    height: 15px;
}

.t-button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}

 /*Responsive*/

 @media screen and (max-width: 480px) {
     .t {
        
     }
 }
